<template>
  <div id="app">
    <div v-show="infoShow" class="overlay"></div>
    <div class="side-menu">
      <button :class="{btnHide: show}"  @click='show = !show' class="side-btn hide-elements" ></button>
      <a :class="{btnHide: show}" href="https://github.com/Resetand" target="_blank" class="github-link side-btn"></a>
      <button @click='infoShow = !infoShow' :class="{btnHide: show}" target="_blank" class="info side-btn"></button>
    </div>
    <div class="progress-wrap">
      <ProgressBar 
      :wrong='wrongCounter'
      :right='rightCounter'
    />
    </div>
    <div class="oper-wrap" :class="{show: show}">
      <OperPick 
        @operchange="operChange"/>
    </div>
    <div class="mian-wrap">
      <Main 
        :curoper='oper'
        :firstmin='firstNumber.min'
        :firstmax='firstNumber.max'
        :secondmin='secondNumber.min'
        :secondmax='secondNumber.max'

        @scoresIncrement='progressChange'
      />
    </div>
   <div class="range-wrap" :class="{show: show}">
      <div class="range-title">First number range pick</div>
      <RangePick
        @changecurlimits='firstNumberChangeLimits'
      /> 
   </div>
   <div class="range-wrap" :class="{show: show}">
     <div class="range-title">Second number range pick</div>
     <RangePick
        @changecurlimits='secondNumberChangeLimits'
      /> 
   </div>

   <transition name="slide-fade">
     <InfoModal v-show="infoShow" @closewindow='infoShow = false'  />
   </transition>

  </div>
</template>
<script>

import ProgressBar from "./components/ProgressBar.vue";
import OperPick from "./components/OperPick.vue";
import Main from "./components/Main.vue";
import RangePick from "./components/RangePick.vue";
import InfoModal from "./components/InfoModal.vue";

export default {
  name: "app",
  components: {
    ProgressBar,
    OperPick,
    Main,
    RangePick,
    InfoModal
  },
  data(){
    return  {
      show: false,
      infoShow: false,

      oper: "plus",

      wrongCounter: 0,
      rightCounter: 0,

      firstNumber:{
        min: 0,
        max: 400
      },
      secondNumber: {
        min: 0,
        max: 400
      }
    }
  },
  methods: {
    operChange(op){
      this.oper = op;
    },
    firstNumberChangeLimits(lims){
      this.firstNumber.min = lims.min;
      this.firstNumber.max = lims.max;
    },
    secondNumberChangeLimits(lims){
      this.secondNumber.min = lims.min;
      this.secondNumber.max = lims.max;
    },
    progressChange(type){
      if (type)
        this.rightCounter++;
      else
        this.wrongCounter++;
    },
  },
};
</script>

