<template>
  <div class="main">
    <div class="choice-btns">
      <button
        @click='changeOper("plusActive")'
         class="btn-oper plus" 
        :class="{'btn-oper_active': state.plusActive}"
       ></button>
      <button
        @click='changeOper("minusActive")' 
         class="btn-oper minus"
        :class="{'btn-oper_active': state.minusActive}" ></button>
      <button
        @click='changeOper("multyActive")' 
         class="btn-oper multy"
        :class="{'btn-oper_active': state.multyActive}"></button>
      <button
        disabled
        @click='changeOper("divActive")' 
         class="disabled btn-oper div"
        :class="{'btn-oper_active': state.divActive}"></button>
    </div>
  </div>
</template>

<script>
export default {
   data() {
     return {
       state: {
        plusActive: true,
        minusActive: false,
        multyActive: false,
        divActive: false,
       },
     }
   },
   methods: {
     changeOper(oper){
       Object.keys(this.state).forEach(key =>{
         this.state[key] = false;
       })
       this.state[oper] = true;
       let curOper;
       switch (oper){
         case "plusActive":
           curOper = 'plus';
           break;
         case "minusActive":
           curOper = 'minus';
           break;
         case "multyActive":
           curOper = 'multy';
           break;
         case "divActive":
           curOper = 'div';
           break;
       }
       this.$emit('operchange', curOper);
     }
   }
};
</script>


<style scoped lang="scss">
.main{
  padding: 10px 20px;
}
.btn-oper{
  transition: .5s;
  outline: none;
  padding: 17px;
  background: unset;
  border: unset;
  cursor: pointer;
  border-radius: 3px;
  position: relative;
}

.btn-oper_active{
  box-shadow: 0 0 0 2px rgba(240, 239, 239, 0.51);
}
.plus{
  background: url(../assets/plus.svg) no-repeat center / 17px;
}
.minus{
  background: url(../assets/minus.svg) no-repeat center  / 20px;
}
.multy{
  background: url(../assets/multy.svg) no-repeat center / 25px;
}
.div{
  background: url(../assets/division.svg) no-repeat center / 18px;
}
.choice-btns{
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.disabled{
  opacity: .3;
  cursor: default;
}
</style>