<template>
  <div class="wrap">
      
      <input class="range-lims range-max" v-model="minLimit"/>
      <div class="range-line">
        <span class="interactiveLine"
          @mousedown.prevent="mouseHold"
          @mousemove.prevent="mouseMove"
          @mouseup.prevent="mouseUnHold"
          @mouseleave="mouseUnHold"
      > </span>
        <div class="cur-range"
          :style="{left: left, right: rigth}"
        >
          <span class="left">
            <span class="range-value">{{minValue}}</span>
          </span>
          <span class="right">
            <span class="range-value">{{maxValue}}</span>
          </span>

        </div>
      </div>
      <input class="range-lims range-min" @keydown="checkVal" v-model="maxLimit"/>
  </div>
</template>

<script>
export default {
  data(){
    return {
      minLimit: 0,
      maxLimit: 400,
      min: 5,
      max: 95,
      holding: false,
      lineX: null,
      lineWidht: null
    }
  },
  computed: {
    minValue(){
      const minLimit = +this.minLimit;
      const maxLimit = +this.maxLimit;
      const width = maxLimit - minLimit;
      if (this.min > 0.5)
        return Math.floor(width * this.min / 100)  + minLimit
      else 
        return minLimit;
    },
    maxValue(){
      const minLimit = +this.minLimit;
      const maxLimit = +this.maxLimit;
      const width = maxLimit - minLimit;
      if (this.max < 99)
        return Math.floor(width * this.max / 100) + minLimit; 
      else 
        return maxLimit;
    },
    left(){
      return this.min + "%"
    },
    rigth(){
      return (100 - this.max) + "%"
    }
  },
  methods: {
    getMinVal(){
      const minLimit = +this.minLimit;
      const maxLimit = +this.maxLimit;
      const width = maxLimit - minLimit;
      if (this.min > 0.5)
        return Math.floor(width * this.min / 100)  + minLimit
      else 
        return minLimit;
    },
    getMaxVal(){
      const minLimit = +this.minLimit;
      const maxLimit = +this.maxLimit;
      const width = maxLimit - minLimit;
      if (this.max < 99)
        return Math.floor(width * this.max / 100) + minLimit; 
      else 
        return maxLimit;
    },
    mouseHold(event){
      this.holding = true;
      this.lineX =  event.target.getBoundingClientRect().x;
      this.lineWidht = event.target.getBoundingClientRect().width;
    },
    mouseMove(event){
      if (this.holding){
        const offset = event.offsetX;
        const currentPosition = offset*100/(this.lineWidht-2);
        const toMin = Math.abs(currentPosition - this.min)
        const toMax = Math.abs(currentPosition - this.max)
        if (toMin < toMax){
          if (currentPosition < this.max){
            this.min = currentPosition;
            if (currentPosition < 0){
              this.min = 0;
            }   
          } else {
            this.mouseUnHold();
            return;
          }
        } else {
          if (this.min < currentPosition){
            this.max = currentPosition;
            if (currentPosition > 100){
              this.max = 100; 
            }
          }  
          else{
            this.mouseUnHold();
            return;
          }
        }
      }
    },
    mouseUnHold(event){
      this.mouseMove(event)
      if (this.holding){
        this.holding = false;
      }
      this.$emit('changecurlimits', {
        min: this.getMinVal(),
        max: this.getMaxVal()
      });
    },
    checkVal(e){
      const valid = new Set(['ArrowLeft', 'ArrowRight', 'ArrowDown', 'ArrowUp', 'Enter', 'Backspace'])
      console.log(e.key)
      const filter = /^\d+$/;
      if (filter.test(e.key) || valid.has(e.key)){
        e.returnValue = true;
      } else {
         e.returnValue = false;
      }
    }
  },
  watch:{
    minLimit(){
      this.$emit('changecurlimits', {
        min: this.getMinVal(),
        max: this.getMaxVal()
      });
    },
    maxLimit(){
      this.$emit('changecurlimits', {
        min: this.getMinVal(),
        max: this.getMaxVal()
      });
    }
  }
}
</script>


<style scoped lang="scss">
.interactiveLine{
  cursor: pointer;
  position: absolute;
  top: -40px;
  left: 0;
  bottom: -40px;
  right: 0;
  background: transparent;
  z-index: 100;
}
.wrap{
  position: relative;
  display: flex;
  align-items: center;
  padding: 40px 0;
}
.range-lims{
 
  font-size: 13px;
  font-weight: 500;
  color: rgb(255, 255, 255);
  background-color: rgba(194, 192, 192, 0.205);
  border: none;
  width: 30px;
  text-align: center;
  border-radius: 4px;
  padding: 1px 4px;
  transition: .2s;
  outline: none;
  &:focus{
    cursor: text !important;
    box-shadow: 0 0 4px rgba(187, 159, 159, 0.363)
  }
  &:hover{
    cursor: pointer;
    box-shadow: 0 0 4px rgba(187, 159, 159, 0.163)
  }
}
.range-line{
  margin: 0 10px;
  width: 100%;
  height: 3px;
  border-radius: 1px;
  background-color: rgba(194, 192, 192, 0.308);
  position: relative;
  .cur-range{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 40px;
    right: 100px;
    background: linear-gradient(0.25turn, #53A13C, #43583c);
    &::before, &::after{
      content: '';
      position: absolute;
      display: block;
      width: 3px;
      height: 15px;
      background-color: #53A13C;
      top: -7px;
      border-radius: 2px;
    }
    &::before{
      left: 0;
    }
    &::after{
      background-color: rgb(67, 88, 60);
      right: 0;
    }
    .left{
      cursor: mouseer;
      position: absolute;
      width: 30px;
      left: -15px;
      top: -38px;
      bottom: -30px;
    }
    .right{
      cursor: mouseer;
      position: absolute;
      width: 30px;
      right: -15px;
      top: -38px;
      bottom: -30px;
      & .range-value{
        background-color: rgb(67, 88, 60);
        &::after{
          background-color: rgb(67, 88, 60);
        }
      }
    }
    .range-value{
      box-shadow: 0 0 10px rgba(0,0,0, .3);
      font-size: 14px;
      border-radius: 2px;
      padding: 1px 5px;
      background-color: #53A13C;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      &::after{
        content: '';
        display: block;
        background-color: #53A13C;
        position: absolute;
        bottom: -3px;
        left: calc(50% - 3px);
        width: 7px;
        height: 7px;
        transform: rotate(45deg);
      }
    }
  }

}
</style>