<template>
  <div class="hello">
    <h1> </h1>
    <div class="line">
      <span :class='{rightAnim: rightAnim}' class="right">Right <span class="dig">{{ right }}</span></span>
      <span :class='{wrongAnim: wrongAnim}' class="wrong"><span class="dig">{{ wrong }}</span> Wrong</span>
      <div class="wrong-line" :style="{left: left}"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    right: Number,
    wrong: Number
  },
  data(){
    return {
      wrongAnim: false,
      rightAnim: false
    }
  },
  computed:{
    left(){
      const all = this.right + this.wrong;
      return 100-(+this.wrong*100/all) + '%'
    },
    
  },
  watch:{
    right(oldVal, newVal){
      this.rightAnim = true;
      setTimeout(()=> this.rightAnim = false, 500)
    },
    wrong(oldVal, newVal){
      this.wrongAnim = true;
      setTimeout(()=> this.wrongAnim = false, 500)
    }
  }
};
</script>


<style scoped lang="scss">
.line{
  transition: .2s;
  border-radius: 2px;
  position: relative;
  width: 100%;
  height: 4px;
  background: #53A13C;
  .wrong-line{
    transition: .2s;
    border-radius: 2px;
    background: #C23535;
    top: 0;
    bottom: 0;
    position: absolute;
    right: 0;
    &::before{
      content: '';
      position: absolute;
      left: 0px;
      top: -4px;
      border-radius: 4px;
      width: 3px;
      height: 12px;
      background-color: #C23535;
    }
  }
}
.wrong{
  color: #C2DFD8;
  .dig{
    color: #C23535;
    margin-right: 7px;
  }
  position: absolute;
  right: 2px;
  top: -25px;
}
.right{
  color: #C2DFD8;
  .dig{
    margin-left: 7px;
    color: #53A13C;
  }
  position: absolute;
  left: 2px;
  top: -25px;
}
.wrongAnim{
  animation: scale 0.23s; 
}
.rightAnim{
  animation: scale 0.23s; 
}
@keyframes scale {
  0% {
    transform: scale(1, 1)
  }
  50%{
    transform: scale(1.3, 1.3)
  }
  100%{
    transform: scale(1, 1)
  }
}
</style>
