<template>
  <div class="main">
    <form @submit.prevent="check">
      <span class="first">{{first}}</span>
      <span class="oper">{{ operToDisplay }}</span>
      <span class="second">{{ second}}</span>
      <span class="eq">=</span>
      <input
         @keydown.prevent="checkVal" 
         v-model="answer" 
         :class='{wrongAnimation: wrongAnimation, rightAnimation: rightAnimation}'
         class="answer" 
         type="text">
    </form>
  </div>
</template>

<script>

function randInt(min, max){
  return Math.floor(min + Math.random() * (max + 1 - min));
}

export default {
  props: {
    curoper: String,
    firstmin: Number,
    firstmax: Number,
    secondmin: Number,
    secondmax: Number
  },
  data(){
    return {
      answer: null,
      first: 0,
      second: 0,
      wrongAnimation: false,
      rightAnimation: false
    }
  },
  created(){
    this.generateRandom();
  },
  computed: { 
    operToDisplay(){
      switch (this.curoper){
        case "plus": return "+"
        case "minus": return "−"
        case "multy": return "×"
        case "div": return "÷"
      }
    },
    
  },
  methods: {
    checkVal(e){
      const valid = new Set(['ArrowLeft', 'ArrowRight', 'ArrowDown', 'ArrowUp', 'Enter', 'Backspace', '-', '+'])
      console.log(e.key)
      const filter = /^\d+$/;
      if (filter.test(e.key) || valid.has(e.key)){
        e.returnValue = true;
      } else {
         e.returnValue = false;
      }
    },
    getAnswer(){
      switch (this.curoper){
        case "plus": return this.first + this.second;
        case "minus": return this.first - this.second;
        case "multy": return this.first * this.second;
        case "div": return this.first / this.second;
      }
    },
    check(){
      if (this.answer !== ''){
        if (this.getAnswer() === parseInt(this.answer, 10)){
          this.generateRandom();
          this.rightAnimation = true;
          setTimeout(()=>this.rightAnimation = false, 300)
          this.$emit('scoresIncrement', true);
        } else {
          this.wrongAnimation = true;
          setTimeout(()=>this.wrongAnimation = false, 400)
          this.$emit('scoresIncrement', false);
        }
      }
      this.answer = '';
    },

    generateRandom(){
      this.second = randInt(this.secondmin, this.secondmax);
      this.first = randInt(this.firstmin, this.firstmax);
    }
  },
    watch: {
      firstmin(){
        this.generateRandom()
      },
      firstmax(){
        this.generateRandom()
      },
      secondmin(){
        this.generateRandom()
      },
      secondmax(){
        this.generateRandom()
      },
  }
};
</script>


<style scoped lang="scss">
.main{
  font-size: 50px;
  font-family: "Fira Code";
}
.oper{
  display: inline-block;
  width: 20px;
  margin: 0 28px;
}
.eq{
  margin: 0 38px;
}
.answer{
  transition: .2s;
  outline: unset;
  text-align: center;
  font-size: 40px;
  width: 100px;
  background-color: rgba(0,0,0,.2);
  padding: 10px 20px;
  border: unset;
  border-radius: 10px
}

.rightAnimation{
   box-shadow: 0 0 14px rgba(80, 252, 0, 0.2);
   animation: pulse .45s;
}

.wrongAnimation{
   box-shadow: 0 0 14px rgba(252, 0, 0, 0.3);
   animation: shake 0.35s; 
}

@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
}
@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
</style>